@import url('https://fonts.googleapis.com/css?family=Oswald');

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.wrapper {
  height: 100vh;
  background: #79a8d8;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.main {
  color: #79a8d8;
  background: #042547;
  border-radius: 5%;
  width: 50%;
}

header {
  height: 25%;
}

.title {
  text-align: center;
  font-size: 50px;
  font-family: 'Oswald', sans-serif;
}

.body {
  height: 50%;
  border-left: #042547 solid 2px;
  border-right: #042547 solid 2px;
}

.form {
  border-right: #042547 solid 2px;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.form .form-control {
  width: 100%;
  
}

.form button {
  width: 75%;  
}

.display {
  font-size: 25px;
}

footer {
  height: 25%;
}

.foot {
  font-size: 50px;
  text-align: center;
  font-family: 'Oswald', sans-serif;
}




